body {
  --link-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__logo {
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.is-active {
  color: var(--link-color);
  margin-left: 0;
}

.nav__link:hover {
  color: var(--link-color);
}

.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

